          <span class="hljs-attr">name</span>: <span class="hljs-string">'test'</span>,
          <span class="hljs-attr">iconOn</span>: <span class="hljs-string">'./statics/SampleData/points/pic.png'</span>,
          <span class="hljs-attr">giscolor</span>: <span class="hljs-string">'#fb7228'</span>,
          <span class="hljs-attr">datauri</span>: <span class="hljs-string">'./statics/SampleData/points/yj.json'</span>
        }
        <span class="hljs-variable language_">this</span>.<span class="hljs-title function_">showPoints</span>(item, flag)
      },
      <span class="hljs-title function_">showPoints</span>(<span class="hljs-params">item, flag</span>) {
        <span class="hljs-keyword">const</span> that = <span class="hljs-variable language_">this</span>
        <span class="hljs-keyword">if</span> (flag) {
          <span class="hljs-title class_">Cesium</span>.<span class="hljs-property">Resource</span>.<span class="hljs-title function_">fetchJson</span>(item.<span class="hljs-property">datauri</span>).<span class="hljs-title function_">then</span>(<span class="hljs-function">(<span class="hljs-params">res</span>) =&gt;</span> {
            <span class="hljs-keyword">const</span> img = <span class="hljs-keyword">new</span> <span class="hljs-title class_">Image</span>()
