            img.<span class="hljs-property">onload</span> = <span class="hljs-function">() =&gt;</span> {
              <span class="hljs-keyword">let</span> datas = []
              <span class="hljs-keyword">let</span> data = {
                <span class="hljs-attr">name</span>: item.<span class="hljs-property">code</span>,
                <span class="hljs-attr">type</span>: <span class="hljs-string">'point'</span>,
                <span class="hljs-attr">clustering</span>: <span class="hljs-literal">true</span>,
                <span class="hljs-attr">data</span>: [],
                <span class="hljs-attr">iconUrl</span>: item.<span class="hljs-property">iconOn</span>,
